body {
  font-family: Arial, sans-serif;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.cat-info {
  text-align: center;
  margin-top: 20px;
}

.cat-info img {
  max-width: 100%;
}

select {
  width: 100%;
  margin-bottom: 10px;
}

.hidden {
  display: none !important;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
  top: calc(50% - 24px);
  left: calc(50% - 24px);
}

.loader-wrapper {
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-color: rgba(0, 0, 0, .706);
  position: fixed;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

